"use client"

import Link from "next/link"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { useEffect, useState, useCallback } from "react"
import {
  TrendingUp,
  TrendingDown,
  Target,
  Calendar,
  MessageSquare,
  Settings,
  Wallet,
  PiggyBank,
  CreditCard,
  LogOut,
  Lock,
  Plus,
  Sparkles,
  RefreshCw,
  Trash2,
  Loader2,
  ShieldCheck
} from "lucide-react"
import Image from "next/image"

interface LinkedAccount {
  bankAccount: string;
  bankName: string;
  createdAt: string;
  locked: boolean;
  connectedId: string;
  accountId: string;
}

export default function DashboardPage() {
  const router = useRouter()
  const [userName, setUserName] = useState("")
  const [isLoading, setIsLoading] = useState(true)
  
  const [linkedAccounts, setLinkedAccounts] = useState<LinkedAccount[]>([])
  const [isLinked, setIsLinked] = useState(false) 
  const [isSynced, setIsSynced] = useState(false) 
  const [hasGoals, setHasGoals] = useState(true)

  const fetchData = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    const userId = localStorage.getItem("userId")

    if (!token) {
      router.push("/login")
      return
    }

    try {
      const storedName = localStorage.getItem("name")
      if (storedName) setUserName(storedName)

      const response = await fetch("http://34.50.7.8:3000/v1/account/user-account", {
        method: "POST",
        headers: {
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ userId: userId }) 
      })

      if (response.ok) {
        const data = await response.json()
        console.log("서버 데이터 확인:", data.result)

        if (data.isSuccess && data.result) {
          setLinkedAccounts(data.result)
          setIsLinked(data.result.length > 0)
          setIsSynced(data.result.length > 0)
        }
      }
    } catch (error) {
      console.error("데이터 로드 실패:", error)
    } finally {
      setIsLoading(false)
    }
  }, [router])

  useEffect(() => {
    fetchData()
  }, [fetchData])

  const handleLogout = async () => {
    if (!confirm("로그아웃 하시겠습니까?")) return
    const accessToken = localStorage.getItem("accessToken")
    const refreshToken = localStorage.getItem("refreshToken")

    try {
      await fetch("http://34.50.7.8:3000/auth/logout", {
        method: "POST",
        headers: {
          "Authorization": `Bearer ${accessToken}`,
          "X-Refresh-Token": refreshToken || ""
        }
      })
    } finally {
      localStorage.clear()
      router.push("/")
    }
  }

  const handleDeleteAccount = async (account: LinkedAccount) => {
    const userId = localStorage.getItem("userId")
    const token = localStorage.getItem("accessToken")

    // 현재 서버 데이터에 ID가 없으므로 알림창으로 상세 안내
    if (!userId || !account.connectedId || !account.accountId) {
      console.error("삭제 실패 데이터 부족:", { userId, account })
      alert(
        `삭제 데이터가 부족합니다.\n\n` +
        `userId: ${userId ? 'OK' : 'NULL (로그인 다시 필요)'}\n` +
        `connectedId: ${account.connectedId ? 'OK' : 'NULL (서버 응답 누락)'}\n` +
        `accountId: ${account.accountId ? 'OK' : 'NULL (서버 응답 누락)'}`
      )
      return
    }

    if (!confirm(`${account.bankName} 계좌를 삭제하시겠습니까?`)) return
    
    try {
      const response = await fetch("http://34.50.7.8:3000/v1/account", {
        method: "DELETE",
        headers: {
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ 
          connectedId: account.connectedId,
          accountId: account.accountId,
          userId: userId 
        })
      })

      const resData = await response.json()

      if (response.ok && resData.isSuccess) {
        alert("성공적으로 삭제되었습니다.")
        fetchData()
      } else {
        alert(`삭제 실패: ${resData.message || '서버 오류'}`)
      }
    } catch (error) {
      console.error("삭제 요청 에러:", error)
      alert("서버와 통신할 수 없습니다.")
    }
  }

  const CardBlurOverlay = ({ message, linkHref, linkText, icon: Icon = Lock }: any) => (
    <div className="absolute inset-0 z-10 flex flex-col items-center justify-center bg-background/40 backdrop-blur-[3px] p-4 text-center animate-in fade-in duration-500">
      <div className="bg-card p-4 rounded-xl shadow-xl border border-border/50 max-w-[220px]">
        <Icon className="h-6 w-6 text-primary mx-auto mb-2" />
        <p className="text-xs font-bold mb-3 leading-tight">{message}</p>
        <Link href={linkHref}>
          <Button size="sm" className="h-8 font-bold px-4">
            {linkText}
          </Button>
        </Link>
      </div>
    </div>
  )

 

  return (
    <div className="min-h-screen bg-background text-foreground">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={110} height={30} className="h-7 w-auto" priority />
          </Link>
          <div className="flex items-center gap-4">
            <Link href="/dashboard/ai-chat"><Button variant="ghost" size="icon"><MessageSquare className="h-5 w-5" /></Button></Link>
            <Link href="/dashboard/settings"><Button variant="ghost" size="icon"><Settings className="h-5 w-5" /></Button></Link>
            <Button variant="ghost" size="icon" onClick={handleLogout}><LogOut className="h-5 w-5 text-muted-foreground" /></Button>
            <Avatar className="h-8 w-8 border">
              <AvatarFallback className="bg-primary/5 text-xs font-bold">{userName ? userName.charAt(0) : "U"}</AvatarFallback>
            </Avatar>
          </div>
        </div>
      </header>

   </div>
  )
}